import React, { memo } from 'react';
import {
  get as _get, pickBy as _pickBy, isEqual as _isEqual
} from 'lodash';
import { compose, withState, withProps, withHandlers } from 'recompose';
import { Button } from '@material-ui/core';
import PropTypes from 'prop-types';

import Tracker from '../../../../utils/tracker';
import withRequest from '../../../../utils/with-request';
import LocationService from '../../../../services/locations';
import LocationFormModal from '../LocationFormModal';

const initialFormData = {
  validationErrors: {},
  name: '',
  address: '',
  neighborhood: '',
  city: '',
  state: '',
  zip_code: '',
  complement: ''
};

const EditLocationButtonBase = memo(({
  isModalOpen, setIsModalOpen, onClose,
  formData, setFormData, updateLocationFetch, updateLocationLoading
}) => {
  const onButtonClick = () => {
    Tracker.hit('EditLocationModal - Opened');
    setIsModalOpen(true);
  };
  return (
    <React.Fragment>
      <Button
        variant="outlined"
        size="large"
        onClick={onButtonClick}
      >
        EDITAR
      </Button>
      {
        isModalOpen && (
          <LocationFormModal
            open={isModalOpen}
            onClose={onClose}
            modalTitle="Editar Local"
            formData={formData}
            setFormData={setFormData}
            onSubmit={() => updateLocationFetch(true).catch(() => {})}
            isLoading={updateLocationLoading}
          />
        )
      }
    </React.Fragment>
  );
});

EditLocationButtonBase.propTypes = {
  isModalOpen: PropTypes.bool,
  setIsModalOpen: PropTypes.func,
  onClose: PropTypes.func,
  formData: PropTypes.object,
  setFormData: PropTypes.func,
  updateLocationFetch: PropTypes.func,
  updateLocationLoading: PropTypes.bool
};

const withUpdateLocation = withRequest(LocationService.updateLocation, {
  name: 'updateLocation',
  init: false,
  options: ({ location, formData, setFormData, previousValues, setAlert, onUpdated, onClose }) => {
    const { validationErrors, ...formFields } = formData;
    const changes = _pickBy(formFields, (value, key) => {
      const previousVal = previousValues[key];
      return !_isEqual(previousVal, value);
    });

    return {
      variables: { id: location.id, changes },
      onRequestFinished: () => {
        setAlert({
          alertMsg: 'Local criado alterado sucesso', alertType: 'success', showAlert: true
        });
        onUpdated();
        onClose();
      },
      onRequestError: error => {
        setAlert({
          alertMsg: 'Erro ao alterar este local', alertType: 'error', showAlert: true
        });
        setFormData({ validationErrors: _get(error, 'errors', {}) });
      }
    };
  }
});

const EditLocationButton = compose(
  withState('isModalOpen', 'setIsModalOpen', false),
  withProps(props => {
    const previousValues = props.location;
    return { previousValues };
  }),
  withState('formData', 'setFormDataRaw', ({ previousValues }) => {
    const { complement, ...requiredFields } = previousValues;

    return {
      ...initialFormData,
      ...requiredFields,
      complement: complement || ''
    };
  }),
  withHandlers({
    setFormData: ({ setFormDataRaw, formData }) => (attrs) => {
      setFormDataRaw({ ...formData, ...attrs });
    }
  }),
  withHandlers({
    onClose: ({ setFormData, setIsModalOpen, previousValues }) => () => {
      Tracker.hit('EditLocationModal - Opened');
      setFormData(previousValues);
      setIsModalOpen(false);
    }
  }),
  withUpdateLocation
)(EditLocationButtonBase);

export default EditLocationButton;
